.main-container {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(243, 245, 248);
}

.ant-collapse-expand-icon {
  font-size: 24px;
  font-weight: bold;
  padding-top: 14px;
}

.gender-radio {
  border: 1px solid black;
  padding: 12px 24px;
  background-color: white;
  margin: 4px 12px 4px 0px;
  border-radius: 7px;
}
